.button {
  width: 10vw;
  background-color: #ececec;
  border: 1px solid #202020;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.1) 0 2px 4px 0;
  color: black;
  font-size: 1.2rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  padding: 5px 10px;
  text-align: center;
  height: 45px;
}

@keyframes onHoverAnimation {
  0% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(102, 62, 145),
      rgb(19, 12, 27)
    );
    text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
  }
  20% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(108, 66, 180),
      rgb(19, 12, 27)
    );
  }
  40% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(114, 68, 180),
      rgb(19, 12, 27)
    );
  }
  60% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(120, 70, 180),
      rgb(19, 12, 27)
    );
  }
  80% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(126, 72, 180),
      rgb(19, 12, 27)
    );
  }
  100% {
    background: radial-gradient(
      ellipse at 50% 50%,
      rgb(132, 80, 180),
      rgb(19, 12, 27)
    );
    text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(255, 255, 255);
  }
}

.button:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0 3px 9px 0;
  cursor: pointer;
}

.success {
  background: radial-gradient(
    ellipse at 50% 50%,
    rgb(102, 62, 145),
    rgb(19, 12, 27)
  );
  border: 1px solid #68277c;
  box-shadow: rgba(0, 0, 0, 0.1) 0 2px 4px 0;
  color: rgb(221, 220, 220);
  width: 13vw;
  font-size: 1.2rem;
  text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
  border-radius: 5px;
}

.success:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0 6px 9px 0;
  animation-name: onHoverAnimation;
  animation-duration: 0.13s;  
  animation-fill-mode: forwards;
  text-shadow: 1px 1px 2px black, 0 0 1em rgb(255, 255, 255);
}

.greenSuccess {
  width: 6vw;
  background: radial-gradient(
    ellipse at 50% 50%,
    rgb(32, 190, 71),
    rgba(0, 53, 20, 0.774)
  );
  box-shadow: rgba(0, 0, 0, 0.1) 0 2px 4px 0;
  color: rgb(221, 220, 220);
  text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
}

.greenSuccess:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0 3px 9px 0;
  text-shadow: 1px 1px 2px black, 0 0 0.3em white;
  background: radial-gradient(
    ellipse at 50% 50%,
    rgb(32, 240, 71),
    rgba(0, 53, 20, 0.774)
  );
}

.greenSuccess {
  width: 6vw;
  background: radial-gradient(
    ellipse at 50% 50%,
    rgb(32, 211, 71),
    rgba(0, 53, 20, 0.774)
  );
  /* border: 1px solid #349258; */
  box-shadow: rgba(0, 0, 0, 0.1) 0 2px 4px 0;
  color: rgb(221, 220, 220);
  text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
}

.greenSuccess:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0 3px 9px 0;
  text-shadow: 1px 1px 2px black, 0 0 0.3em white;
  background: radial-gradient(
    ellipse at 50% 50%,
    rgb(32, 240, 71),
    rgba(0, 53, 20, 0.774)
  );
} 

.cancel {
  background-color: #aa1327;
  border: 1px solid #680818;
  color: #fff;
  text-shadow: 1px 1px 2px black, 0 0 0.2em white;
}
