.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.loginForm {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 30vw auto;
  padding: 50px 0 38px;
  width: 30%;
  height: 550px;
  border-radius: 12px;
  color: azure;
  background-color: rgba(49, 59, 74, 0.7);
}

.input {
  width: 85%;
  margin-bottom: 50px;
}

.btnGroup {
  width: 85%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  margin-bottom: 40px;
  font-size: 26px;
  font-weight: bold;
  text-align: center;
}
