.block {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-size: 70px;
  margin-bottom: 25px;
  width: 280px;
  height: 396px;
  border: 2px solid rgba(240, 255, 255, 0.4);
  color: azure;
  box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, 0.4);
  transition: transform 0.2s;
}

.block:hover {
  transform: scale(1.05);
}
