.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.loginForm {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
  width: 30%;
  height: 350px;
  border-radius: 12px;
  color: azure;
  background-color: rgba(49, 59, 74, 0.7);
}

.input {
  width: 85%;
  margin-bottom: 50px;
  text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
}

.input2 {
  width: 85%;
  margin-bottom: 50px;
  text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
}

.btnGroup {
  width: 85%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  margin-bottom: 40px;
  font-size: 26px;
  font-weight: bold;
  text-align: center;
  text-shadow: 1px 1px 2px black, 0 0 0.3em rgb(0, 0, 0);
}
