.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile {
  display: flex;
  width: 45%;
  height: 45vh;
  padding: 25px;
  color: white;
  border-radius: 20px;
  background-color: rgba(52, 49, 73, 0.7);
}

.flexContainer {
  margin-right: 25px;
  flex: 2;
  display: flex;
  flex-direction: column;
}

.item1 {
  display: flex;
  margin-bottom: 25px;
  padding: 15px;
  width: 100%;
  height: 100%;
  background-color: rgba(65, 79, 101, 0.9);
}

.item1__img {
  flex: 1;
  margin-right: 25px;
  height: 100%;
  border: 1px solid white;
}

.item1__content {
  flex: 2;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
}

.item1__content__group {
  display: flex;
  flex-direction: column;
}

.item1__content__group__id {
  align-self: flex-end;
  margin-bottom: 5px;
}

.item1__content__group__name {
  font-size: 1.4rem;
  margin-bottom: 5px;
}

.item1__content__group__status {
  font-size: 1rem;
  margin-bottom: 5px;
}

.item1__content_time {
  align-self: flex-end;
  margin-bottom: 5px;
}

.item2 {
  padding: 15px;
  width: 100%;
  height: 100%;
  background-color: rgba(65, 79, 101, 0.9);
}

.item3 {
  padding: 15px;
  flex: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(65, 79, 101, 0.9);
}

.backBtn {
  position: absolute;
  top: 0;
  left: 0;
}
